<style>
@import url("/assets/styles/style.css");

.router-anim-enter-active {
  animation: coming 0.5s;
  opacity: 0;
}
.router-anim-leave-active {
  animation: going 0.5s;
  opacity: 1;
}

@keyframes going {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(-50px);
    opacity: 0;
  }
}
@keyframes coming {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
</style>

<script>
import { ref } from "vue";

export default {
  data() {
    return {
      isActive: false,
      isVisible: false,
      disableBtn: false,
      text: "To Do",
    };
  },
  setup() {
    const newTodo = ref("");
    const todos = ref([]);
    function addNewTodo() {
      todos.value.push({
        id: Date.now(),
        done: false,
        content: newTodo.value,
      });
      newTodo.value = "";
    }
    function toggleDone(todo) {
      todo.done = !todo.done;
    }
    function removeTodo(index) {
      todos.value.splice(index, 1);
    }
    function markAllDone() {
      todos.value.forEach((todo) => (todo.done = true));
    }
    function removeAllTodos() {
      todos.value = [];
    }
    return {
      todos,
      newTodo,
      addNewTodo,
      toggleDone,
      removeTodo,
      markAllDone,
      removeAllTodos,
    };
  },
  methods: {
    toggle: function () {
      this.isActive = !this.isActive;
    },
  },
};
</script>
